body{
  background-color: aliceblue;
  background-image: url(bg.jpg);
}
h1{
  color: rgb(88, 182, 245);
  font-style: oblique;
  font-family: sans-serif;
  font-size: 35px;
}
.App {
  background-color:rgb(116, 116, 116);
  min-height: 500px;
  width: 500px;
  margin: 50px auto;
  border-radius: 10px;
  text-align: center;

}

#todolist input{
  background-color: rgb(230, 230, 230);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: rgb(0, 0, 0);
}

#todolist input:focus{
  outline:none;
}
#todolist button:focus{
  outline:none;
}

#todolist button{
  background-color:  rgb(88, 182, 245); 
  width: 100px;
  height: 50px;
  font-size: 18px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;

}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
