.list{
    color: #fff;
    width: 350px;
    margin: 0px auto;
    background-color: rgb(30, 157, 241);
    border-radius: 5px;
    padding: 1;
}
.list p{
    padding: 0px;
    position: relative;
    transition: ease-in-out;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 15px;
    bottom: 35px;
}
.faicons{
    margin: 0 5px;
    font-size: 20px;
    cursor: pointer;
}
.itemsAnim-enter{
    opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
    opacity: 1;
    transition: opacity 200ms ease-in
}
.itemsAnim-leave{
    opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
    opacity: 0.01;
    transition: opacity 200ms ease-in
}